.resume-container {
  display: flex;
  align-items: center;
  flex-direction: column;
  justify-content: center;
  min-height: fit-content;
  margin: 120px 0;
}

.resume-card {
  display: flex;
  height: 360px;
  width: 1000px;
  /* background-color: tomato; */
}

.resume-bullets {
  height: 100%;
  box-shadow: 15px 0 9px -15px #1f2235;
  width: 320px;
}
.resume-bullets-details {
  flex-grow: 1;
  width: 600px;
  overflow: hidden;
  padding: 0 0 0 80px;
}
.experience-description {
  margin: 10px 0 0 0;
  text-align: justify;
  max-width: 100%;
}

.resume-details-carousal {
  transition: transform 1s ease-out;
}

.bullet-container {
  display: flex;
  align-items: center;
  height: 100%;
  width: 100%;
  position: relative;
}

.bullets {
  width: 80%;
  position: relative;
  z-index: 2;
}

.bullet {
  display: flex;
  align-items: center;
  background-color: #1f2235;
  height: 40px;
  margin: 15px 0;
  padding: 0 8px;
  border-radius: 20px;
  width: 30px;
  transition: width 0.6s ease;
  cursor: pointer;
}

.bullet-label {
  font-size: 15px;
  white-space: nowrap;
  font-family: "Poppins SemiBold";
  /* margin-bottom: 17px; */
}
.bullet-logo {
  height: 16px;
  margin: 0 30px 0 0;
  /* margin-bottom: 17px; */
}

.bullets-icons {
  width: 34px;
  height: 100%;
  z-index: 1;
  background-color: #1f2235;
  position: absolute;
}
.selected-bullet {
  color: wheat;
  width: 100%;
}
.resume-screen-container {
  display: flex;
  flex-direction: column;
  justify-content: space-evenly;
  height: 360px;
  width: 100%;
}
.resume-heading {
  display: flex;
  flex-direction: column;
}
.resume-main-heading {
  position: relative;
  display: flex;
  justify-content: space-between;
}
.heading-bullet {
  position: absolute;
  left: -30px;
  height: 15px;
  width: 15px;
  /* top: 5px; */
  background-color: var(--dark-orange);
  border-radius: 24px;
  margin-top: 10px;
}

.heading-bullet-start {
  display: flex;
  justify-content: space-between;
  flex-direction: column;
}

.heading-within{
    display: flex;
    flex-direction:row;
}

.heading-within span{
    color: var(--dark-orange);
    font-weight: bold;
    margin: 0 0px 0 0;
    font-size: 20px;
}
.heading-date{
    background-color: var(--dark-orange);
    border-radius: 41px;
    width: 103px;
    text-align: center;
    justify-content: center;
    margin-left: 2px;
    padding: 4px 12px;
    color: white;
    font-size: 14px;
}
.resume-sub-heading{
    font-size: 14px;
    font-family: "Poppins SemiBold";
    color: #111;
    margin: 10px 0 0 0;
}

.resume-heading-description{
    font-size: 12px;
    text-align: justify;
}

.skill-parent{

    display: flex;
    flex-direction: column;
    position: relative;
    width: 50%;
}

.programming-skills-container{
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-self: space-between;
    flex-wrap: wrap;
}
.skill-percentage{
    position: relative;
    height: 15px;
    width: 70%;
    background-color: var(--dark-orange);
}

.skill-percentage .active-percentage-bar{
   height: 15px;
   transition: 0.0s ease;
   position: absolute;
   background-color: #1f2235;
}

.skill-parent{
    font-family: "Poppins SemiBold";
    font-size: 16px;
    color: var(--dark-orange);
}

@media screen and (max-width:1110px) {
    .resume-card{
        width: 800px;
    }

    .resume-bullets-details{
        padding: 0 0 0 70px;
    }
    
}

@media screen and (max-width:1023px) {
    .resume-content{
        display: flex;
        flex-direction: column;
        align-items: center;
    }

    .bullets{
        width: 90%;
    }
    .resume-card{
        height: fit-content;
        display: flex;
        flex-direction: column;
        margin: 30px 0;
        align-items: center;
        width: 90%;
    }

    .resume-bullets-details{
        height: 360px;
        width: fit-content;
        padding: 0;
    }

    .resume-bullets{
        width: 100%;
        margin: 30px 0;
    }

    .selected-bullet{
        width: 100%;

    }
}



